<template>
    <div 
        class="card animate__animated"  
        :class="{ 'is-active animate__fadeOutRight': isActive }" 
        :title="index" 
        :id="index"
    >

        <div class="card-content" 
			@click.prevent="copyclipboard(`${index}`), removeList(index)" 
			@mouseover="showMore = true" 
			@mouseout="showMore = false">

            <div class="media">
                <div class="media-content">
					<span class="icon has-text-warning">
						<i class="fas fa-star"></i>
					</span>
					<span class="icon has-text-warning">
						<i class="fas fa-star"></i>
					</span>
					<span class="icon has-text-warning">
						<i class="fas fa-star"></i>
					</span>
					<span class="icon has-text-warning">
						<i class="fas fa-star"></i>
					</span>
					<span class="icon has-text-warning">
						<i class="fas fa-star"></i>
					</span>

                    <p class="subtitle is-5 review-name">{{ review.content.split(" ").splice(0,35).join(" ") }}
                    <span v-show="showMore" class="subtitle is-5 review-name">{{ review.content.split(" ").splice(35,100000).join(" ") }}</span>

                    </p>
 
                    <p :id="`${index}`" class="subtitle is-5 review-email">{{ review.content }}</p>
                    
                </div>
            </div>

        </div>
        
    </div>
</template>

<script>
	import { ref } from 'vue';

	export default {
		props: {
			isLoading: {
				type: Boolean,
				default: false,
			},

			index: {
				type: Number,
            },
            
            review: {
                type: Object,
            }

		},

		setup(props, {emit}) {

			let isActive = ref(false);

			let showMore = ref(false);

			function removeList(id) {
				setTimeout( () => {
					document.getElementById(id).style.display = "none";
				}, 1000);
			}

			function copyclipboard(id) {
				isActive.value = true;

				emit('copyclipboard', id);
			}

			return {
				isActive,
				copyclipboard,
				removeList,
				showMore
			}
		}
	}
</script>

<style>

	@keyframes fadein {
		from {opacity: 0;}
		to   {opacity: 1;}
	}

	.is-active {
		background: green !important;
	}

	.card {
		margin-bottom: 13px;
		position: relative;
		-webkit-transition: all ease-in-out 0.3s;
		-moz-transition: all ease-in-out 0.3s;
		transition: all ease-in-out 0.3s;
	}

    .card p {
		-webkit-transition: all ease-in-out 0.3s;
		-moz-transition: all ease-in-out 0.3s;
		transition: all ease-in-out 0.3s;
    }

	.media {
		display: flex !important;
		align-items: center !important;
	}

	.card:before {
		content: attr(title);
		position: absolute;
		right: 37px;
		top: -1px;
		font-size: 6rem;
		font-family: 'Black Ops One', cursive;
		font-weight: 500;
		color: #d6d4dd3d;
		transform: rotate(0deg);
	}

	.card:after {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		background: #7957d5 !important;
		width: 127px;
		height: 5px;
	}

	.subtitle.is-5,
	.review-name {
		margin-bottom: 6px !important;
		font-size: 0.9rem !important;
		line-height: 1.7;
	}

	.review-email {
		display: none;
	}

	.review-username {
		font-weight: 400;
		font-size: 13px !important;
	}

	.card:hover input[type=text],
	.card:hover {
		cursor: pointer;
		background: #7957d5 !important;
	}	

	.card:hover p span,
    .card:hover p {
        color: #fff;
    }

</style>